.landing {
  .prototype {
    position: relative;
    overflow: hidden;
    background-color: #000;
    color: #fff; }
  .prototype__container {
    position: relative;
    padding-top: 300px;
    padding-bottom: 140px; } }
@media ($max_laptop) {
  .prototype__container {
    padding-top: 225px;
    padding-bottom: 104px; } }
@media ($max_tablets--landscape) {
  .prototype__container {
    padding-top: 180px;
    padding-bottom: 83px; } }

.prototype__logo_box {
  position: absolute;
  top: -3%;
  left: -10%;
  width: 555px;
  height: 451px; }

@media ($max_laptop) {
  .prototype__logo_box {
    width: 416px;
    height: 338px; } }
@media ($max_tablets--landscape) {
  .prototype__logo_box {
    width: 333px;
    height: 270px; } }

