.pricing {
  background-color: #1c1c1d;
  color: #ffffff;
  padding-top: 150px;
  padding-bottom: 218px; }
@media ($max_laptop) {
  .pricing {
    padding-top: 112px;
    padding-bottom: 163px; } }
@media ($max_tablets--landscape) {
  .pricing {
    padding-top: 90px;
    padding-bottom: 130px; } }
.pricing__feedbacks {
  margin-bottom: 80px; }
@media ($max_laptop) {
  .pricing__feedbacks {
    margin-bottom: 60px; } }
@media ($max_tablets--landscape) {
  .pricing__feedbacks {
    margin-bottom: 48px; } }
.pricing__feedbacks_box {
  position: relative;
  height: 310px; }
@media ($max_laptop) {
  .pricing__feedbacks_box {
    height: 232px; } }
@media ($max_tablets--landscape) {
  .pricing__feedbacks_box {
    height: 186px; } }
.pricing__feedback {
  position: absolute;
  opacity: 0;
  transform: scale(1.05);
  transform-origin: center;
  &.state-active {
    opacity: 1;
    transform: none;
    transition: opacity 1.5s ease, transform 2s cubic-bezier(0.4, 0.07, 0.08, 0.94); } } //0.8s
.pricing__feedback_text {
  margin-bottom: 45px;
  font-size: 36px;
  font-weight: 300;
  line-height: 1.4;
  &:after, &:before {
    content: "";
    position: absolute;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center; }
  &:before {
    left: 0;
    top: 0;
    width: 31px;
    height: 24px;
    background-image: url(../img/icons/quotation_marks_top.svg);
    transform: translateY(-120%) translateX(-120%); }
  &:after {
    width: 29px;
    height: 26px;
    right: 0;
    background-image: url(../img/icons/quotation_marks_bottom.svg);
    transform: translateY(100%) translateX(100%); } }
@media ($max_laptop) {
  .pricing__feedback_text {
    margin-bottom: 34px;
    font-size: 27px;
    &:before {
      width: 23px;
      height: 18px; }
    &:after {
      width: 22px;
      height: 20px; } } }
@media ($max_tablets--landscape) {
  .pricing__feedback_text {
    margin-bottom: 27px;
    font-size: 22px;
    &:before {
      width: 18px;
      height: 14px; }
    &:after {
      width: 18px;
      height: 16px; } } }
@media (max-width: 470px) {
  .pricing__feedback_text {
    margin-bottom: 27px;
    font-size: 20px;
    &:before {
      width: 18px;
      height: 14px; }
    &:after {
      width: 18px;
      height: 16px; } } }

