.pricing__dot {
  position: relative;
  float: left;
  width: 40px;
  height: 10px;
  margin-right: 20px;
  cursor: pointer;
  &:after, &:before {
    content: "";
    position: absolute;
    left: 0;
    top: 4px;
    height: 2px;
    bottom: 0;
    width: 100%; }
  &:after {
    content: "";
    z-index: 1;
    background-color: rgba(#45464f, 0.40); }
  &:before {
    z-index: 10;
    background-color: #fff;
    transform-origin: left center;
    transform: scale(0);
    opacity: 0;
    transition: opacity 0.3s ease, transform 0.0s 0s linear; }
  &.state-loading-dot {
    &:after {
      background-color: rgba(#45464f, 0.9); }
    &:before {
      transform: scale(1);
      opacity: 1;
      transition: transform 5s linear; } }
  &.state-active-dot {
    &:after {
      content: "";
      background-color: white;
      transition: background-color 0.25s ease; }
    &:before {
      content: "";
      transform: none; } } }
