.landing {
  .about {
    position: relative;
    background-color: #131313;
    color: #fff; }
  .about__container {
    padding-top: 135px; } }
@media ($max_laptop) {
  .about__container {
    padding-top: 100px; } }
@media ($max_tablets--landscape) {
  .about__container {
    padding-top: 80px; } }
@media (max-width: 390px) {
  .about__container {
    padding-top: 50px; } }

.about__subheader {
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.6px;
  margin-bottom: 58px;
  display: inline-block;
  position: relative;
  color: $sky;
  transform: translateY(-20px) scale(.95);
  opacity: 0;
  transition: opacity .7s ease, transform .7s ease-out, color .9s ease;

  &.appeared-content {
    color: #fff;
    transform: none;
    opacity: 1; }

