@media ($max_laptop) {
  .about__subheader {
    font-size: 9px;
    margin-bottom: 43px; } }
@media ($max_tablets--landscape) {
  .about__subheader {
    font-size: 8px;
    margin-bottom: 34px; } }
@media (max-width: 390px) {
  .about__subheader {
    font-size: 7px;
    margin-bottom: 24px; } }

.about__info {
  z-index: 10;
  position: relative;
  left: -4px;
  font-size: 48px;
  font-weight: 300;
  margin-bottom: 78px;
  opacity: 0;
  transform: translateY(-5%) scale(0.985);
  color: $sky;
  transition: opacity 1s ease, transform 1s ease-out, color 1.2s ease;
  &.appeared-content {
    color: #fff;
    transform: none;
    opacity: 1; } }

@media ($max_laptop) {
  .about__info {
    font-size: 32px;
    margin-bottom: 58px; } }
@media ($max_tablets--landscape) {
  .about__info {
    font-size: 26px;
    margin-bottom: 46px; } }
@media (max-width: 390px) {
  .about__info {
    font-size: 22px;
    margin-bottom: 34px; } }

