.features {
  position: relative;
  padding: 160px 40px 120px 40px;
  background-color: #eff3fc;
  text-align: center; }

@media ($max_laptop) {
  .features {
    padding: 120px 40px 90px 40px; } }
@media ($max_tablets--landscape) {
  .features {
    padding: 96px 40px 72px 40px; } }
@media ($max_tablets--portrait) {
  .features {
    padding: 80px 40px 60px 40px; } }
@media (max-width: 550px) {
  .features {
    padding: 40px 20px 30px 20px; } }

.features__content {
  max-width: 1400px; }
@media ($max_desktop) {
  .features__content {
    max-width: 1200px; } }
@media ($max_laptop) {
  .features__content {
    max-width: 1050px; } }
@media ($max_tablets--landscape) {
  .features__content {
    max-width: 750px; } }
@media ($max_tablets--portrait) and ($landscape) {
  .features__content {
    max-width: 600px; } }
@media (max-width: 550px) {
  .features__content {
    max-width: none; } }

.features__box {
  overflow: hidden;
  display: inline-block;
  text-align: left;
  position: relative;
  padding: 170px 120px;
  border-radius: 20px;
  background-color: white;
  box-shadow: none;
  transition: box-shadow 1s ease, transform 1s ease;
  transform: scale(0.995);
  transition-delay: .5s;
  &.state-content {
    box-shadow: 0 5px 13px rgba(96, 111, 142, 0.26);
    transform: none; } }

@media ($max_laptop) {
  .features__box {
    padding: 127px 90px;
    border-radius: 15px;
    &.state-content {
      box-shadow: 0 3px 9px rgba(96, 111, 142, 0.26);
      transform: none; } } }
@media ($max_tablets--landscape) {
  .features__box {
    padding: 101px 72px;
    border-radius: 12px;
    &.state-content {
      box-shadow: 0 3px 7px rgba(96, 111, 142, 0.26);
      transform: none; } } }
@media ($max_tablets--portrait) {
  .features__box {
    padding: 60px 0;
    border-radius: 10px;
    &.state-content {
      box-shadow: 0 3px 6px rgba(96, 111, 142, 0.26);
      transform: none; } } }
@media (max-width: 550px) {
  .features__box {
    padding: 60px 20px;
    border-radius: 10px;
    width: 100%;
    &.state-content {
      box-shadow: 0 3px 6px rgba(96, 111, 142, 0.26);
      transform: none; } } }
.features__illustration_box {
  position: absolute;
  right: 0; //-5%
  top: -12%; }
@media ($max_desktop) {
  .features__illustration_box {
    top: -9.5%;
    width: 760px; }
  .features__illustration_box svg {
    width: 800px;
    height: 800px; } }
@media (max-width: 1410px) {
  .features__illustration_box {
    width: 690px; }
  .features__illustration_box svg {
    width: 750px;
    height: 750px; } }
@media ($max_laptop) {
  .features__illustration_box svg {
    width: 712px;
    height: 712px; } }
@media (max-width: 1180px) {
  .features__illustration_box {
    width: 570px; }
  .features__illustration_box svg {
    width: 650px;
    height: 650px; } }
@media (max-width: 1080px) {
  .features__illustration_box {
    width: 530px; }
  .features__illustration_box svg {
    width: 570px;
    height: 570px; } }
@media ($max_tablets--landscape) {
  .features__illustration_box {
    width: 530px; }
  .features__illustration_box svg {
    width: 570px;
    height: 570px; } }
@media (max-width: 950px) {
  .features__illustration_box {
    top: -6%;
    width: 400px; }
  .features__illustration_box svg {
    width: 420px;
    height: 420px; } }
@media (max-width: 850px) {
  .features__illustration_box {
    top: -4%;
    width: 330px; }
  .features__illustration_box svg {
    width: 350px;
    height: 350px; } }
@media (max-width: 800px) {
	.features__illustration_box {
	  top: -2%;
	  width: 280px; }
	.features__illustration_box svg {
	  width: 300px;
	  height: 300px; } }
@media ($max_tablets--portrait) {
  .features__illustration_box {
    width: 420px;
    left: 50%;
    top: 20%;
    right: auto;
    transform: translateX(-50%); }
  .features__illustration_box svg {
    width: 420px;
    height: 420px; } }
@media ($max_phones--small) {
  .features__illustration_box {
    width: 420px;
    left: 50%;
    top: 23%;
    right: auto;
    transform: translateX(-50%); }
  .features__illustration_box svg {
    width: 420px;
    height: 420px; } }
@media ($max_phones--tiny) {
  .features__illustration_box {
    width: 380px;
    left: 50%;
    top: 25%;
    right: auto;
    transform: translateX(-50%); }
  .features__illustration_box svg {
    width: 380px;
    height: 380px; } }
#shadow {
  opacity: 0.3;
  transition: opacity 1s ease-in-out;
  //animation-delay: 2s
  .appeared-content & {
    opacity: 1;
    animation: shadow_blur 5s linear 1s infinite; } }
#bar {
  transition: transform 1s ease-in-out;
  transform: translateY(-10%);
  //animation-delay: 2s
  .appeared-content & {
    transform: translateY(0);
    animation: hover 5s linear 1s infinite; } }
@keyframes hover {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(-5%); }
  100% {
    transform: translateY(0); } }

@keyframes shadow_blur {
  0% {
    //fill: #f0fcff
    opacity: 1; }
  50% {
    //fill: #d9e9ff
    opacity: 0.3; }
  100% {
    //fill: #f0fcff
    opacity: 1; } }
#bar-input {
  stroke: #ebebeb;
  stroke-width: 1.5;
  stroke-dashoffset: 199;
  stroke-dasharray: 199;
  fill: #ebebeb;
  fill-opacity: 0;
  transition: fill-opacity 1.5s ease, stroke-dashoffset 1s ease;
  transition-delay: 1.3s, 0.6s;
  .appeared-content & {
    fill-opacity: 1;
    stroke-dashoffset: 0; } }
#bar-submit {
  stroke: #4d69fe;
  stroke-width: 1.5;
  stroke-dashoffset: 96;
  stroke-dasharray: 96;
  fill: #4d69fe;
  fill-opacity: 0;
  transition: fill-opacity 1.5s ease, stroke-dashoffset 1s ease;
  transition-delay: 1.9s, 1s;
  .appeared-content & {
    fill-opacity: 1;
    stroke-dashoffset: 0; } }
#bar-circle {
  stroke: #b4b6ff;
  stroke-width: 1.5;
  stroke-dashoffset: 251;
  stroke-dasharray: 251;
  fill: #b4b6ff;
  fill-opacity: 0;
  transition: fill-opacity 1.5s ease, stroke-dashoffset 1s ease;
  transition-delay: 0.7s, 0s;
  .appeared-content & {
    fill-opacity: 1;
    stroke-dashoffset: 0; } }
#bar-triangle {
  stroke: #ffecb4;
  stroke-width: 1.5;
  stroke-dashoffset: 251;
  stroke-dasharray: 251;
  fill: #ffecb4;
  fill-opacity: 0;
  transition: fill-opacity 1.5s ease, stroke-dashoffset 1s ease;
  transition-delay: 1s, 0.3s;
  .appeared-content & {
    fill-opacity: 1;
    stroke-dashoffset: 0; } }

.features__title {
  margin-bottom: 60px; }
@media ($max_laptop) {
  .features__title {
    margin-bottom: 45px; } }
@media ($max_tablets--landscape) {
  .features__title {
    margin-bottom: 36px; } }
@media ($max_tablets--portrait) {
  .features__title {
    text-align: center; } }
.features__title__word {
  display: inline-block;
  opacity: 0;
  transform: translateY(-15px) scale(0.985);
  transition: opacity 1s ease, transform 1s ease;
  @for $i from 1 through 3 {
    &:nth-child(#{$i}) {
      transition-delay: #{170 * ($i - 1)}ms; } }
  .appeared-content & {
    opacity: 1;
    transform: none; } }
.features__info {
  max-width: 600px;
  margin-bottom: 190px;
  font-size: 24px;
  font-weight: 300;
  line-height: 1.4;
  opacity: 0;
  transform: translateY(-5%) scale(0.985);
  color: $sky;
  transition: opacity 1s ease, transform 1s ease-out, color 1.2s ease;
  &.appeared-content {
    color: #000;
    transform: none;
    opacity: 1; } }
@media ($max_laptop) {
  .features__info {
    max-width: 450px;
    margin-bottom: 142px;
    font-size: 18px; } }
@media ($max_tablets--landscape) {
  .features__info {
    max-width: 360px;
    margin-bottom: 113px;
    font-size: 14px; } }
@media ($max_tablets--portrait) {
  .features__info {
    text-align: center;
    margin: 0 auto 350px auto; } }
@media ($max_phones--tiny) {
  .features__info {
    text-align: center;
    margin: 0 auto 320px auto; } }
.features__row {
  @include clear_list;
  display: flex;
  justify-content: center;
  align-items: start;
  margin-bottom: 100px;
  &:last-child {
    margin-bottom: 0; } }
@media ($max_laptop) {
  .features__row {
    margin-bottom: 75px;
    &:last-child {
      margin-bottom: 0; } } }
@media ($max_tablets--landscape) {
  .features__row {
    margin-bottom: 60px;
    &:last-child {
      margin-bottom: 0; } } }
@media ($max_phones--small) {
  .features__row {
    margin-bottom: 30px;
    &:last-child {
      margin-bottom: 0; } } }
.features__item {
  width: 25%;
  padding-right: 80px;
  opacity: 1;
  @for $i from 1 through 4 {
    &:nth-child(#{$i}) .features__item_title {
      transition-delay: #{100 + 170 * ($i - 1)}ms; }
    &:nth-child(#{$i}) .features__item_text {
      transition-delay: #{170 * ($i - 1)}ms; } } }
@media ($max_laptop) {
  .features__item {
    padding-right: 60px; } }
@media ($max_tablets--landscape) {
  .features__item {
    padding-right: 48px; } }
@media ($max_tablets--portrait) {
  .features__item {
    padding: 0 40px;
    width: 35%;
    min-width: 200px;
    opacity: 0; } }
.features__item_title {
  margin-bottom: 13px;
  font-size: 16px;
  font-weight: 700;
  color: $sky;
  transform: translateY(-20px) scale(.95);
  opacity: 0;
  transition: opacity .95s cubic-bezier(.25,.1,.2,1), transform .95s cubic-bezier(.25,.1,.2,1), color .95s ease;
  .appeared-content & {
    color: #000;
    transform: none;
    opacity: 1; } }
@media ($max_laptop) {
  .features__item_title {
    margin-bottom: 10px;
    font-size: 12px; } }
@media ($max_tablets--landscape) {
  .features__item_title {
    margin-bottom: 8px;
    font-size: 10px; } }
.features__item_text {
  font-size: 16px;
  font-weight: 300;
  line-height: 1.4;
  color: $sky;
  transform: translateY(20px) scale(.95);
  opacity: 0;
  transition: opacity .95s cubic-bezier(.25,.1,.2,1), transform .95s cubic-bezier(.25,.1,.2,1), color 1.1s ease;
  transition-delay: .1s;
  .appeared-content & {
    color: #000;
    transform: none;
    opacity: 1; } }
@media ($max_laptop) {
  .features__item_text {
    font-size: 12px; } }
@media ($max_tablets--landscape) {
  .features__item_text {
    font-size: 10px; } }
.features__features {
  display: flex;
  flex-direction: column; }
@media ($max_tablets--portrait) {
  .features__features {
    flex-direction: column-reverse; } }
.features__dots {
  @include clearfix;
  display: none;
  flex-direction: row;
  justify-content: center;
  margin-top: 45px;
  padding: 20px 0;
  & label {
    &:nth-child(3), &:nth-child(4) {
      display: none; } } }
@media ($max_tablets--portrait) {
  .features__dots {
    display: flex;
    margin-top: 27px;
    padding: 12px 0; } }
@media ($max_phones--small) {
  .features__dots {
    & label {
      &:nth-child(3), &:nth-child(4) {
        display: block; } } } }
.features__table {
  display: flex;
  flex-direction: column; }
.features__dot_input {
  position: absolute;
  opacity: 0; }
@media ($max_tablets--portrait) {
  #features__dot_first:checked ~ .features__dots label[for='features__dot_first'] .features__dot {
    &:after {
      content: "";
      background-color: black; } }
  #features__dot_second:checked ~ .features__dots label[for='features__dot_second'] .features__dot {
    &:after {
      content: "";
      background-color: black; } }
  #features__dot_first:checked ~ .features__table {
    & .features__item {
      &:nth-child(1) {
        transition: opacity 1.5s ease;
        opacity: 1; }
      &:nth-child(2) {
        transition: opacity 1.5s ease;
        opacity: 1; }
      &:nth-child(3) {
        position: absolute; }
      &:nth-child(4) {
        position: absolute; } } }
  #features__dot_second:checked ~ .features__table {
    & .features__item {
      &:nth-child(1) {
        position: absolute; }
      &:nth-child(2) {
        position: absolute; }
      &:nth-child(3) {
        transition: opacity 1.5s ease;
        opacity: 1; }
      &:nth-child(4) {
        transition: opacity 1.5s ease;
        opacity: 1; } } } }
@media ($max_phones--small) {
  #features__dot_first:checked ~ .features__dots label[for='features__dot_first'] .features__dot {
    &:after {
      content: "";
      background-color: black; } }
  #features__dot_second:checked ~ .features__dots label[for='features__dot_second'] .features__dot {
    &:after {
      content: "";
      background-color: black; } }
  #features__dot_third:checked ~ .features__dots label[for='features__dot_third'] .features__dot {
    &:after {
      content: "";
      background-color: black; } }
  #features__dot_fourth:checked ~ .features__dots label[for='features__dot_fourth'] .features__dot {
    &:after {
      content: "";
      background-color: black; } }
  #features__dot_first:checked ~ .features__table {
    & .features__item {
      &:nth-child(1) {
        transition: opacity 1.5s ease;
        opacity: 1;
        position: static; }
      &:nth-child(2) {
        transition: none;
        opacity: 0;
        position: absolute; }
      &:nth-child(3) {
        transition: none;
        opacity: 0;
        position: absolute; }
      &:nth-child(4) {
        transition: none;
        opacity: 0;
        position: absolute; } } }
  #features__dot_second:checked ~ .features__table {
    & .features__item {
      &:nth-child(1) {
        transition: none;
        opacity: 0;
        position: absolute; }
      &:nth-child(2) {
        transition: opacity 1.5s ease;
        opacity: 1;
        position: static; }
      &:nth-child(3) {
        transition: none;
        opacity: 0;
        position: absolute; }
      &:nth-child(4) {
        transition: none;
        opacity: 0;
        position: absolute; } } }
  #features__dot_third:checked ~ .features__table {
    & .features__item {
      &:nth-child(1) {
        transition: none;
        opacity: 0;
        position: absolute; }
      &:nth-child(2) {
        transition: none;
        opacity: 0;
        position: absolute; }
      &:nth-child(3) {
        transition: opacity 1.5s ease;
        opacity: 1;
        position: static; }
      &:nth-child(4) {
        transition: none;
        opacity: 0;
        position: absolute; } } }
  #features__dot_fourth:checked ~ .features__table {
    & .features__item {
      &:nth-child(1) {
        transition: none;
        opacity: 0;
        position: absolute; }
      &:nth-child(2) {
        transition: none;
        opacity: 0;
        position: absolute; }
      &:nth-child(3) {
        transition: none;
        opacity: 0;
        position: absolute; }
      &:nth-child(4) {
        transition: opacity 1.5s ease;
        opacity: 1;
        position: static; } } } }
.features__dot {
  position: relative;
  float: left;
  width: 40px;
  height: 2px;
  padding: 7px 0;
  margin-right: 20px;
  cursor: pointer;
  &:after, &:before {
    content: "";
    position: absolute;
    left: 0;
    height: 2px;
    transform: translateY(-50%);
    width: 100%; }
  &:after {
    content: "";
    z-index: 1;
    background-color: rgba(#45464f, 0.40);
    transition: background-color .5s ease; } }
@media ($max_laptop) {
  .features__dot {
    width: 30px;
    height: 2px;
    margin-right: 15px; } }
@media ($max_tablets--landscape) {
  .features__dot {
    width: 24px;
    height: 2px;
    margin-right: 12px; } }
@media ($max_tablets--portrait) {
  .features__dot {
    width: 24px;
    height: 2px;
    margin-right: 12px; } }
